* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: blueviolet;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  margin: 2rem auto;
  color: white;
  width: 400px;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
}

input {
  padding: 0.8rem 1rem;
  margin: 0.5rem 0;
  outline: none;
}

button {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  text-transform: uppercase;
  margin-top: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 1rem;
}

.container h1 {
  font-size: 1.4rem;
  text-align: center;
  /* padding: 0.7rem 0; */
  text-transform: uppercase;
}

.todo_list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: start;
  background-color: burlywood;
  color: black;
  padding: 0.5rem;
  margin: 1rem 0;
}

.detiles {
  display: flex;
  flex-direction: column;
}

.trash {
  color: red;
  cursor: pointer;
}
